/* colors (from LIFE Designguide) */
/* fonts */
.progress-bars {
  background: #033030;
  width: 100%;
  height: 130px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 1;
}
.progress-bars__bar {
  width: 33.3%;
  display: flex;
  flex-direction: column;
  margin: 0 1.666rem 0 0;
}
.progress-bars__bar:first-child {
  margin-lefT: 1.666rem;
}
.progress-bars__bar .ui.header {
  font-weight: normal;
  color: white;
}
.progress-bars__bar .ui.progress:last-child {
  margin-bottom: 0;
}
